"use client"
import React, { useState } from "react";
import { Grid, Box } from "@mui/material";
import HomeCards from "./components/HomeCards";

const properties = [
  {
    id: 1,
    title: "Diamond Manor Apartment",
    type: "For Rent",
    address: "145 Willow St #2, Cambridge, MA 02141",
    beds: 2,
    baths: 2,
    sqft: 0,
    dateAvailable: "12/10/2025",
    price: 3000,
    homeType: "Apartment",
    image: "https://via.placeholder.com/300x200",
  },
  {
    id: 2,
    title: "Villa Bay",
    type: "For Sale",
    address: "145 Willow St #2, Cambridge, MA 02141",
    beds: 2,
    baths: 2,
    sqft: 150,
    price: 500000,
    homeType: "Condo",
    image: "https://via.placeholder.com/300x200",
  },
];

const MapViewIndex = () => {
  const [filters, setFilters] = useState({
    type: "All",
    price: "All",
    beds: "All",
    baths: "All",
    homeType: "All",
  });

  const filteredProperties = properties.filter((property) => {
    return (
      (filters.type === "All" || property.type === filters.type) &&
      (filters.price === "All" ||
        (filters.price === "<3000" && property.price < 3000) ||
        (filters.price === "3000-5000" &&
          property.price >= 3000 &&
          property.price <= 5000) ||
        (filters.price === ">5000" && property.price > 5000)) &&
      (filters.beds === "All" || property.beds === parseInt(filters.beds)) &&
      (filters.baths === "All" || property.baths === parseInt(filters.baths)) &&
      (filters.homeType === "All" || property.homeType === filters.homeType)
    );
  });

  return (
    <Box sx={{ padding: 3 }}>
      <Filter filters={filters} setFilters={setFilters} />
      <Grid container spacing={3}>
        {filteredProperties.map((property) => (
          <HomeCards key={property.id} property={property} />
        ))}
      </Grid>
    </Box>
  );
};

export default MapViewIndex;
